// pehelp.defaultValueExpression: "This setting allows you to assign a default answer value based on an expression. The expression can include basic calculations - `{q1_id} + {q2_id}`, Boolean expressions, such as `{age} > 60`, and functions: `iif()`, `today()`, `age()`, `min()`, `max()`, `avg()`, etc. The value determined by this expression serves as the initial default value that can be overridden by a respondent's manual input." => "Toto nastavenie vm umouje priradi predvolen hodnotu odpovede na zklade vrazu. Vraz me obsahova zkladn vpoty - '{q1_id} + {q2_id}', boolovsk vrazy, ako naprklad '{age} > 60', a funkcie: 'iif()', 'today()', 'age()', 'min()', 'max()', 'avg()' at. Hodnota uren tmto vrazom sli ako poiaton predvolen hodnota, ktor je mon prepsa manulnym vstupom respondenta."
// pehelp.resetValueIf: "Use the magic wand icon to set a conditional rule that determines when a respondent's input is reset to the value based on the \"Default value expression\" or \"Set value expression\" or to the \"Default answer\" value (if either is set)." => "Pomocou ikony arovnho prtika nastavte podmienen pravidlo, ktor uruje, kedy sa vstup respondenta obnov na hodnotu na zklade hodnoty \"Vraz predvolenej hodnoty\" alebo \"Vraz nastavenej hodnoty\" alebo hodnoty \"Predvolen odpove\" (ak je nastaven)."
// pehelp.setValueIf: "Use the magic wand icon to set a conditional rule that determines when to run the \"Set value expression\" and dynamically assign the resulting value as a response." => "Pomocou ikony arovnho prtika nastavte podmienen pravidlo, ktor uruje, kedy spusti \"Nastavi vraz hodnoty\" a dynamicky priradi vsledn hodnotu ako odpove."
// pehelp.setValueExpression: "Specify an expression that defines the value to be set when the conditions in the \"Set value if\" rule are met. The expression can include basic calculations - `{q1_id} + {q2_id}`, Boolean expressions, such as `{age} > 60`, and functions: `iif()`, `today()`, `age()`, `min()`, `max()`, `avg()`, etc. The value determined by this expression can be overridden by a respondent's manual input." => "Zadajte vraz, ktor definuje hodnotu, ktor sa m nastavi, ke s splnen podmienky pravidla \"Nastavi hodnotu ak\". Vraz me obsahova zkladn vpoty - '{q1_id} + {q2_id}', boolovsk vrazy, ako naprklad '{age} > 60', a funkcie: 'iif()', 'today()', 'age()', 'min()', 'max()', 'avg()' at. Hodnota uren tmto vrazom me by prepsan manulnym vstupom respondenta."
