// datetimemask.pattern: "The pattern can contain separator characters and the following placeholders: `m` - for month number; `mm` - for month number, with leading zero for single-digit values; `d` - for day of the month; `dd` - for day of the month, with leading zero for single-digit values; `yy` - for the last two digits of the year; `yyyy` - for a four-digit year." => "Vzor me obsahova oddeovacie znaky a nasledujce zstupn symboly: \"m\" - pre slo mesiaca; \"mm\" - pre slo mesiaca, s vodnou nulou pre jednocifern hodnoty; \"d\" - pre de v mesiaci; \"dd\" - pre de v mesiaci s poiatonou nulou pre jednocifern hodnoty; \"yy\" - pre posledn dve slice roka; \"rrrr\" - na tvorcifern rok."
// numericmask.decimalSeparator: "A symbol used to separate the fractional part from the integer part of a displayed number." => "Symbol pouvan na oddelenie zlomkovej asti od celoselnej asti zobrazenho sla."
// numericmask.thousandsSeparator: "A symbol used to separate the digits of a large number into groups of three." => "Symbol pouvan na oddelenie slic vekho sla do skupn po troch."
// numericmask.precision: "Limits how many digits to retain after the decimal point for a displayed number." => "Obmedzuje, koko slic sa m zachova za desatinnou iarkou pre zobrazen slo."
// currencymask.prefix: "One or several symbols to be displayed before the value." => "Jeden alebo niekoko symbolov, ktor sa maj zobrazi pred hodnotou."
// currencymask.suffix: "One or several symbols to be displayed after the value." => "Jeden alebo viac symbolov, ktor sa zobrazia za hodnotou."
