// imagepicker.contentMode: "Choose between \"Image\" and \"Video\" to set the content mode of the media selector. If \"Image\" is selected, ensure that all options provided are image files in the following formats: JPEG, GIF, PNG, APNG, SVG, BMP, ICO. Similarly, if \"Video\" is selected, ensure that all options are direct links to video files in the following formats: MP4, MOV, WMV, FLV, AVI, MKV. Please note that YouTube links are not supported for video options." => "Vyberte si medzi \"Obrzok\" a \"Video\" a nastavte reim obsahu vberu mdi. Ak je vybrat monos \"Obrzok\", uistite sa, e vetky poskytnut monosti s obrazov sbory v nasledujcich formtoch: JPEG, GIF, PNG, APNG, SVG, BMP, ICO. Podobne, ak je vybrat monos \"Video\", uistite sa, e vetky monosti s priamymi odkazmi na video sbory v nasledujcich formtoch: MP4, MOV, WMV, FLV, AVI, MKV. Upozorujeme, e monosti vide nie s podporovan odkazmi na YouTube."
